import React, { useState } from 'react';
import { Form, Input, Select, Button } from 'antd';
import { injectIntl } from 'react-intl';
import { addBillingInfo } from './../../services';

const Option = Select.Option;
const countries = require('./../../utils/countries').default;

const options = Object.keys(countries).map(key => {
  return (
    <Option key={key} value={key}>
      {countries[key].name}
    </Option>
  );
});

export const InvoiceFormCmp = props => {
  const {
    nameUser,
    intl: { messages },
    form: { getFieldDecorator },
  } = props;

  const [form, setForm] = useState({
    name: '',
    address: '',
    country: 'ES',
    vattax: '',
  });
  const [loading, setLoading] = useState(false);

  const handleOk = e => {
    const { form, next } = props;
    try {
      form.validateFields(async (err, values) => {
        if (err) {
          return;
        }
        values.continent = countries[values.country].continent;
        setLoading(true);
        await addBillingInfo(values);
        setLoading(false);
        next();
      });
    } catch (error) {
      console.log(error.message);
      setLoading(false);
    }
  };

  const tailFormItemLayout = {
    wrapperCol: {
      xs: {
        span: 24,
        offset: 0,
      },
      sm: {
        span: 14,
        offset: 6,
      },
    },
  };

  return (
    <div style={{ padding: '50px 0px 0 0' }}>
      <Form onSubmit={handleOk} style={{ margin: '0 auto' }} className="login-form">
        {/* nombre, dirección, pais, VAT/TAX number */}
        <Form.Item>
          {getFieldDecorator('name', {
            initialValue: nameUser,
            rules: [
              {
                required: true,
                message: 'Please input your Name!',
              },
            ],
          })(<Input placeholder={messages.profile.name} />)}
        </Form.Item>
        <Form.Item>
          {getFieldDecorator('address', {
            initialValue: form.address,
            rules: [
              {
                required: true,
                message: 'Please input your Address!',
              },
            ],
          })(<Input placeholder={messages.profile.address} />)}
        </Form.Item>
        <Form.Item hasFeedback>
          {getFieldDecorator('country', {
            initialValue: form.country,
            rules: [
              {
                required: true,
                message: 'Please select your country!',
              },
            ],
          })(
            <Select
              showSearch
              optionFilterProp="children"
              filterOption={(input, option) =>
                option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
              placeholder={messages.profile.country}
            >
              {options}
            </Select>
          )}
        </Form.Item>
        <Form.Item>
          {getFieldDecorator('vattax', {
            initialValue: form.vattax,
            rules: [
              {
                required: true,
                message: 'Please input your VAT/TAX number!',
              },
            ],
          })(<Input placeholder={messages.profile.vattax} />)}
        </Form.Item>
        <Form.Item {...tailFormItemLayout}>
          <Button loading={loading} type="primary" htmlType="submit" className="btn-cta">
            {!loading ? 'Continue' : messages.signup.btnSignUpLoad}
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

const InvoiceForm = Form.create()(injectIntl(InvoiceFormCmp));

export default InvoiceForm;
