import { getSuscriptionsActives } from './../../../services';
import './../index.css';

const becomePeriod = value => {
  switch (value) {
    case 1:
      return 'Month';
    case 3:
      return 'Quarter';
    case 6:
      return '6 Month';
    case 12:
      return 'Annual';
    case 24:
      return 'Biannual';

    default:
      break;
  }
};

const { Step } = Steps;

// minPrice: 5

